.main-groups {
    margin: 10px 12px;
    padding: 0;
    list-style: none;
}

.main-group {
    background: #ffffff;
    border-radius: 5px;
    margin: 10px auto;
    width: 80%;
    box-shadow: 0 2px 11px 0 rgba(0,0,0,.1);
}

.main-group__title {
    text-align: center;
    font-family: inherit;
    font-weight: bold;
    margin: 0;
    padding: 0;
}

.main-group__links {
    margin: 0;
    padding: 0 0 10px 0;
    list-style: none;
}

.main-group__footer {
    text-align: right;
    font-weight: bold;
    padding: 0 10px 6px;
    margin: 0;
    margin-top: -34px;
    padding-bottom: 11px;
}

.footer__number {
    color: white;
    background: red;
    border-radius: 50%;
    padding: 2px;
    display: inline-block;
    width: 21px;
    text-align: center;
    height: 22px;
}

.main-group__links li {
    margin: 4px 12px;
    padding: 1.5px 3px;
}

.main-group__links li a:hover,
.main-group__links li a:active {
    color: black;
}
.filter-options {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: flex-start;
    width: 80%;
    margin: 0 auto;
}

.filter-option {
    background-color: #6441A5;
    color: #fff;
    border-radius: 4px;
    padding: 0 5px;
    margin: 3px;
    cursor: pointer;
    text-decoration: none;
    font-size: 14px;
}

.filter-option:hover {
    color: #6441A5;
    background-color: #fff;
    border: 0.5px solid #6441A5;
}
.Form {
    width: 100%;
}

.Form__Row {
    width: 100%;
    margin: 10px auto;
}

.Btn {
    margin-top: 20px;
    height: 40px;
    border: 0;
    font-weight: bold;
    color: white;
    cursor: pointer;
    border-radius: 3px;
    width: 200px;
}

.Btn--Save {
    background-color: #6441a5;
}

.Btn--Save:active,
.Btn--Save:hover {
    background-color: #EF5350;
}

.Input {
    padding: 10px;
    border: 0;
    background-color: #EBEBEB;
    border-bottom: 2px solid red;
    outline: none;
    width: 200px;
}

.Input:focus {
    border-bottom: 2px solid #6441a5;
}

.Form__Select {
    height: 40px;
    width: 200px;
    cursor: pointer;
}
.modal {
    position: fixed;
    top: 0;
    left: 0;
    width:100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.6);
}

.modal-main {
    position:fixed;
    background: white;
    width: 80%;
    height: auto;
    top:50%;
    left:50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
}

.display-block {
    display: block;
}

.display-none {
    display: none;
}

.closeModal {
    float: right;
    padding: 6px;
    cursor: pointer;
    font-weight: bold;
    margin-right: 6px;
    margin-top: 2px;
}
* {
    box-sizing: border-box;
    font-family: Roboto;
}

.app {
    background: #EBEBEB;
    border-radius: 3px;
    box-sizing: content-box;
    margin: 20px;
}

.Links {
    display: inline-block;
    width: 50%;
    border-right: 4px solid #FFF;
    box-sizing: border-box;
}

.Form {
    display: inline-block;
    width: 50%;
    vertical-align: top;
    box-sizing: border-box;
    text-align: center;
}

.main-title {
    text-align: center;
}

.icon-add {
    visibility: hidden;
    cursor: pointer;
    text-align: right;
    position: -webkit-sticky;
    position: sticky;
    bottom: 0;
    margin-top: 0;
}

@media screen and (max-width: 500px) {
    .app {
        display: flex;
        flex-wrap: wrap;
    }

    .app > div {
        width:100%;
    }

    .Links {
        border-right:none;
    }
    .Form {
        width: 100%;
    }

    .DesktopForm {
        display: none;
    }

    .icon-add {
        visibility: visible;
    }
}
